import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import './css/cards.css';
import './css/baground.css';
import './css/hame.css';
import NavScroll from '../compunents/nav bar/nav';

const Profile = () => {
    const { user } = useSelector(state => state.user);
    const { _id } = useParams();

    useEffect(() => {
        // Add logic to fetch user data by _id if needed
        // fetchUserById(_id); // Uncomment if you have a function to fetch user by ID
    }, [_id]);

    if (!user) {
        return <div>Loading...</div>; // or handle redirect or error
    }

    return (
        <div>
            <NavScroll />

            <div style={{ marginTop: '1%' }}>
                <div className="square-box3"></div>
                <div className="square-box4"></div>
                <div className="square-box"></div>
                <div className="square-box2"></div>

                <h1 className='name'>Welcome, {user.name}!</h1>
                {/* Display other user information */}
            </div>
        </div>
    );
};

export default Profile;
